html,body{height:97%;margin:0;padding:10px;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.container{margin:30px}.flex-column{display:flex;flex-direction:column;align-items:center;justify-content:center}.flex-row{display:flex;flex-direction:row;align-items:center;justify-content:center}.loading{height:80%;width:100%;position:fixed;z-index:100;text-align:center;border-radius:4px;padding:30px 50px;align-items:center;justify-content:center}

